.productCard__wrapper {
    width: 30%;
    padding: 10px;
    border: 1px solid gray;
    margin: auto;
    word-wrap: break-word;
}

.productCard__wrapper h4{
    font-size: 1.2rem;
    font-weight: 900;
}

.productCard__img {
    object-fit: cover;
    flex-shrink: 1;
    width: 100%;
    height: 281px;
    margin-bottom: 20px;
}

.productCard__Rating, .productCard__price {
    margin: 20px 0;
}

.productCard__price {
    font-size: 1.2rem;
}

.productCard__button {
    padding: 6px 10px;
    cursor: pointer;
    border-radius: 25px;
    background-color: #ffa430;
    border: none;
    transition: all .2s ease-in;
}

.productCard__button:hover{
    color: white;
    background-color: #f1b668;
    transform: translate(2px, -2px);
}

.productCard__button:focus,
.productCard__button:active{
    outline: none;
}

@media screen and (max-width: 1147px){
    .productCard__wrapper{
        width: 45%;
    }
}

@media screen and (max-width: 700px){
    .productCard__wrapper{
        width: 90%;
    }
}