.cartItem__item{
    border-bottom: 1px solid gray;
    padding: 10px 0;
    margin-top: 10px;
}

li{
    list-style-type: none;
    height: 100px;
    display: flex;
    gap: 5px;
    align-items: center;
}

li img{
    height: 100%;
    width: 100px;
    object-fit: cover;
}

.cartItem__item:nth-child(1){
    border-top: 1px solid gray;
}

.cartItem__button{
    background-color: orange;
    margin-left: auto;
    padding: 4px 8px;
    border-radius: 4px;
    border: none;
    cursor: pointer;
}

.cartItem__button:focus, .cartItem__button:active{
    outline: none;
}